.profile__name {
  max-width: 500px;
  word-wrap: break-word;
  font-weight: 500;
  font-size: 42px;
  line-height: 1.33;
  color: #fff;
  margin: 0;
  position: relative;
  grid-area: name;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 767px) {
  .profile__name {
    font-size: 27px;
    line-height: 56px;
  }
}
