@import url(./__logo/header__logo.css);

.header {
  padding: 45px 15% 0;
  width: 100%;
  box-sizing: border-box;
}

.header::after {
  content: "";
  display: block;
  border: 0;
  border-bottom: 1px solid #545454;
  opacity: 0.7;
  padding-top: 40px;
  box-sizing: border-box;
}

@media screen and (max-width: 1023px) {
  .header {
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
    padding: 28px 27px 31px;
  }

  .header::after {
    display: none;
  }
}
