.popup__image {
  min-width: 25vw;
  min-height: 25vh;
  max-width: 75vw;
  max-height: 75vh;
  margin: 0;
  z-index: 3;
  color: white;
  border-radius: 5px;
  background: rgb(115, 160, 223);
  background: url("../../../images/Logo-white.svg"), linear-gradient(180deg, #01b7ffe7 0%, #0178ffea 50%, #013cffe7 100%);
  background-size: 50%, contain;
  background-repeat: no-repeat;
  background-position: center;
}
